import React, { Component } from "react";

import "./tableOfContents.css";

function scrollToSection(event) {
  if (typeof event === "object" && event !== null) event.preventDefault();

  if (window.innerWidth <= 768) hide();

  const targetId = event.target.getAttribute("href");
  const target = document.querySelector(targetId);
  target.scrollIntoView({ behavior: "smooth" });
}

// This function listens for the scroll event on the window and checks which section is currently in view by comparing its position to the current scroll position. It then finds the corresponding link in the table of contents by looking for the class that matches the section's id attribute and adds the active class to that link. The active class is removed from all other links in the table of contents.
function highlightLink() {
  const sections = document.querySelectorAll(".section");

  sections.forEach((section) => {
    const currentScrollPosition = window.pageYOffset + window.innerHeight / 2;

    const sectionTop = section.offsetTop;
    const sectionBottom = sectionTop + section.offsetHeight;

    if (
      currentScrollPosition >= sectionTop &&
      currentScrollPosition < sectionBottom
    ) {
      // Hide the other links
      const links = document.querySelectorAll("#table-of-contents a");
      links.forEach((link) => {
        link.classList.remove("active");
      });

      // Highlight this link
      const id = section.getAttribute("id");
      const activeLink = document.querySelector(`#table-of-contents #id${id}`);
      activeLink.classList.add("active");
    }
  });
}

function show(event) {
  if (typeof event === "object" && event !== null) event.preventDefault();

  document.querySelector("#showButton").classList.add("hidden");
  document.querySelector("#table-of-contents").classList.remove("hidden");
}

function hide(event) {
  if (typeof event === "object" && event !== null) event.preventDefault();

  document.querySelector("#showButton").classList.remove("hidden");
  document.querySelector("#table-of-contents").classList.add("hidden");
}

class Home extends Component {
  render() {
    const theme = this.props.theme;
    const links = this.props.links;
    window.addEventListener("scroll", highlightLink);
    if (window.innerWidth <= 768) {
      // mobile
      return (
        <div id="left-tray" style={{ backgroundColor: theme.body }}>
          <ul id="table-of-contents" class="hidden">
            {links.map((link) => {
              return (
                <li>
                  <a
                    href={"#" + link[0]}
                    id={"id" + link[0]}
                    style={{ color: theme.text }}
                    onClick={scrollToSection}
                  >
                    {link[1]}
                  </a>
                </li>
              );
            })}
            <li>
              <a
                href="/"
                onClick={hide}
                style={{ color: theme.secondaryText, fontSize: "12px" }}
              >
                Hide ◀
              </a>
            </li>
          </ul>
          <a
            href="/"
            id="showButton"
            //class="hidden"
            onClick={show}
            style={{
              color: theme.secondaryText,
              fontSize: "24px",
              textDecoration: "none",
              marginLeft: "4px",
              marginRight: "0px",
            }}
          >
            ≡
          </a>
        </div>
      );
    } else {
      return (
        <div id="left-tray" style={{ backgroundColor: theme.body }}>
          <ul id="table-of-contents" /*class="hidden"*/>
            {links.map((link) => {
              return (
                <li>
                  <a
                    href={"#" + link[0]}
                    id={"id" + link[0]}
                    style={{ color: theme.text }}
                    onClick={scrollToSection}
                  >
                    {link[1]}
                  </a>
                </li>
              );
            })}
            <li>
              <a
                href="/"
                onClick={hide}
                style={{ color: theme.secondaryText, fontSize: "12px" }}
              >
                Hide ◀
              </a>
            </li>
          </ul>
          <a
            href="/"
            id="showButton"
            class="hidden"
            onClick={show}
            style={{
              color: theme.secondaryText,
              fontSize: "24px",
              textDecoration: "none",
              marginLeft: "4px",
              marginRight: "0px",
            }}
          >
            ≡
          </a>
        </div>
      );
    }
  }
}

export default Home;
