function UrlExists(url) {
  var http = new XMLHttpRequest();
  http.open("HEAD", url, false);
  http.send();
  return http.status;
}

var shajs = require("sha.js");

class Home extends Component {
  unlock(event) {
    event.preventDefault(); // Ignore form submission, instead redirect to next page.
    var pw = document.getElementById("accessCodeField").value;
    if (pw === "") {
      alert("Please enter a unique access code.");
      return false;
    }

    // hash
    var hash = shajs("sha256").update(pw).digest("hex");
    console.log(hash);
    // test the hash
    var url = "./" + hash + "/index.html";
    var status = UrlExists(url);

    if (status === 200) {
      window.location.href = "/#/" + hash; // Success!
    } else {
      alert("Oops! Your access code is invalid or expired. Please try again.");
    }
    return false; // Needs to be false, so that the page actually redirects
  }

  render() {
    const theme = this.props.theme;
    return (
      <div className="project-page-main">
        <Header theme={theme} />
        <Helmet>
          <title>{seo.prefix}Locked</title>
        </Helmet>

        <Fade bottom duration={2000} distance="40px">
          <div className="project-page-container">
            <div className="project-page-div">
              <h1 style={{ textAlign: "center" }}>Heads up!</h1>
              <p>
                The page you are trying to reach contains confidential
                information. You'll need to enter a unique access code to
                continue.
              </p>
              <p>
                If you'd like to request an access code, please{" "}
                <a
                  href="https://www.linkedin.com/in/tonyqm/"
                  style={{ color: theme.text }}
                >
                  reach out on LinkedIn
                </a>
                . Thanks!
              </p>

              <form class="customForm" onSubmit={this.unlock}>
                <input
                  type="password"
                  id="accessCodeField"
                  class="customTextField"
                  placeholder="Access Code"
                />
                <input type="submit" class="customButton" value="Submit" />
              </form>

              <div style={{ display: "flex", justifyContent: "center" }}>
                <Button
                  text="Return Home"
                  newTab={false}
                  href={"/#/home"}
                  theme={this.props.theme}
                  className="portfolio-repo-btn"
                />
              </div>
            </div>
          </div>
        </Fade>
        <Footer theme={this.props.theme} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default Home;
