import React from "react";
import "./SocialMedia.css";
// import { socialMediaLinks } from "../../portfolio";
import styled from "styled-components";

import ReactGA from "react-ga4";
import { Fade } from "react-reveal";

const IconWrapper = styled.span`
  i {
    background-color: ${(props) => props.backgroundColor};
  }
  &:hover i {
    background-color: ${({ theme }) => theme.text};
    transition: 0.3s ease-in;
  }
`;

function SMButton({ linkname, link, fontAwesomeIcon, backgroundColor }) {
  return (
    <a
      href={link}
      className={`icon-button`}
      target="_blank"
      rel="noopener noreferrer"
      onClick={() =>
        ReactGA.event({
          category: "Social Button",
          action: "SM " + linkname,
        })
      }
    >
      <IconWrapper backgroundColor={backgroundColor}>
        <i className={`fab ${fontAwesomeIcon}`}></i>
      </IconWrapper>
    </a>
  );
}

export default function socialMedia(props) {
  return (
    <div className="social-media-div">
      <Fade bottom duration={1000} distance="40px">
        <SMButton
          linkname="LinkedIn"
          link="https://www.linkedin.com/in/tonyqm/"
          fontAwesomeIcon="fa-linkedin-in"
          backgroundColor="#0077B5"
        />
      </Fade>
      <Fade bottom duration={1500} distance="40px">
        <SMButton
          linkname="Email"
          link="mailto:tqmeng.ucsd@gmail.com"
          fontAwesomeIcon="far fa-envelope"
          backgroundColor="#0077B5"
        />
      </Fade>
      <Fade bottom duration={2000} distance="40px">
        <SMButton
          linkname="Resume"
          link="https://drive.google.com/file/d/1VAoegFHFvB8b51DWC5Qo-xQZu8gShzZj/view?usp=sharing"
          fontAwesomeIcon="far fa-file"
          backgroundColor="#0077B5"
        />
      </Fade>

      {/* {socialMediaLinks.map((media) => { return ( <a>...</a> ); })} */}
    </div>
  );
}
