import React, { Component } from "react";
import "./Splash.css";
import { Redirect } from "react-router-dom";
import LoaderLogo from "../../components/Loader/LoaderLogo.js";

function AnimatedSplash(props) {
  return (
    <div className="logo_wrapper">
      <div className="screen" style={{ backgroundColor: props.theme.text }}>
        <LoaderLogo id="logo" theme={props.theme} />
      </div>
    </div>
  );
}

class Splash extends Component {
  constructor(props) {
    super(props);
    this.state = {
      redirect: false,
    };
  }

  componentDidMount() {
    this.id = setTimeout(() => this.setState({ redirect: true }), 5500);
  }

  componentWillMount() {
    clearTimeout(this.id);
  }

  render() {
    return this.state.redirect /* If it's ready to redirect then... */ ? (
      <div>
        {(() => {
          if (
            this.props.renderAfter !== null &&
            this.props.renderAfter !== undefined
          ) {
            return this.props.renderAfter; /* Go to a specified page */
          } else {
            return <Redirect to="/home" />; /* Go home */
          }
        })()}
      </div>
    ) : (
      <AnimatedSplash theme={this.props.theme} /> /* Start here */
    );
  }
}

export default Splash;
