import React, { Component } from "react";

class ProjectIcon extends Component {
  render() {
    const project = this.props.project;
    /*const theme = this.props.theme;*/

    let text = this.props.project.logo_path;
    if (text.substring(text.length - 3) === "svg") {
      return (
        <object
          width="100%"
          height="100%"
          type="image/svg+xml"
          style={{
            maxWidth: "100%",
            maxHeight: "100%",
            transform: "scale(100%, 100%)",
          }}
          data={require(`../../assests/images/${project.logo_path}`)}
        >
          {project.alt_name}
        </object>
      );
    } else {
      return (
        <img
          style={{
            maxWidth: "100%",
            maxHeight: "100%",
            transform: "scale(100%, 100%)",
          }}
          src={require(`../../assests/images/${project.logo_path}`)}
          alt={project.alt_name}
        />
      );
    }
  }
}

export default ProjectIcon;
