import React, { Component } from "react";
import { Route, Switch, HashRouter } from "react-router-dom";

import ReactGA from "react-ga4";
import { createBrowserHistory } from "history";

import Home from "../pages/home/HomeComponent";
import Splash from "../pages/splash/Splash";
import Education from "../pages/education/EducationComponent";
import Experience from "../pages/experience/Experience";
//import Opensource from "../pages/opensource/Opensource";
import Contact from "../pages/contact/ContactComponent";
import Projects from "../pages/projects/Projects";
import Playground from "../pages/projects/playground";

import WeatherHub from "../pages/projectPages/WeatherHub";
import Taskathon from "../pages/projectPages/Taskathon";
import ExaminingCS from "../pages/projectPages/examiningCS";
import Inspiration from "../pages/projectPages/inspiration";
import DesignProcess from "../pages/projectPages/process";
import RedesignWorkflow from "../pages/projectPages/redesignWorkflow";
import Bloxtopia from "../pages/projectPages/bloxtopia";
import Zillow from "../pages/projectPages/zillow";

import Locked from "../pages/projectPages/locked";
import SecretPage from "../pages/projectPages/newPage";
import Calendar from "../pages/projectPages/calendar";
import Error404 from "../pages/errors/error404/Error";

export default class Main extends Component {
  /* Google Analytics */
  componentDidMount() {
    ReactGA.initialize("G-ZMK7H6TMWR");

    // Set up history
    const history = createBrowserHistory();

    // Track current page
    this.trackPageView(window.location.href);

    // Track page changes
    history.listen(() => {
      this.trackPageView(window.location.href);
    });
  }

  trackPageView = (page) => {
    ReactGA.set({ page });
    ReactGA.send({ hitType: "pageview", page });
    console.log("Navigate: " + page);
  };
  /* end Google Analytics */

  render() {
    const theme = this.props.theme;
    console.log(theme);
    return (
      <div>
        <HashRouter basename="/">
          <Switch>
            <Route
              path="/"
              exact
              render={(props) => <Splash {...props} theme={this.props.theme} />}
            />
            {/* MAIN PAGES */}
            <Route
              path="/home"
              render={(props) => <Home {...props} theme={this.props.theme} />}
            />
            <Route
              path="/experience"
              exact
              render={(props) => (
                <Experience {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/education"
              render={(props) => (
                <Education {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/about"
              render={(props) => (
                <Contact {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/projects"
              render={(props) => (
                <Projects {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/playground"
              render={(props) => (
                <Playground {...props} theme={this.props.theme} />
              )}
            />
            {/* PROJECT PAGES */}
            <Route
              path="/weatherhub"
              render={(props) => (
                <WeatherHub {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/taskathon"
              render={(props) => (
                <Taskathon {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/bloxtopia"
              render={(props) => (
                <Bloxtopia {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/zillow"
              render={(props) => <Zillow {...props} theme={this.props.theme} />}
            />
            {/* OTHER DESIGN PAGES */}
            <Route
              path="/examining-cities-skylines"
              render={(props) => (
                <ExaminingCS {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/inspiration"
              render={(props) => (
                <Inspiration {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/design-process"
              render={(props) => (
                <DesignProcess {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="/redesigning-how-you-work"
              render={(props) => (
                <RedesignWorkflow {...props} theme={this.props.theme} />
              )}
            />
            {/* CALENDAR */}
            <Route
              path="/calendar"
              render={(props) => (
                <Splash
                  {...props}
                  theme={this.props.theme}
                  renderAfter={<Calendar {...props} theme={this.props.theme} />}
                />
              )}
            />
            {/* SPECIAL PAGES */}
            <Route
              path="/locked"
              render={(props) => <Locked {...props} theme={this.props.theme} />}
            />
            <Route
              path="/unlocked"
              render={(props) => (
                <SecretPage {...props} theme={this.props.theme} />
              )}
            />
            <Route
              path="*"
              render={(props) => (
                <Error404 {...props} theme={this.props.theme} />
              )}
            />
            {/* RECYCLE BIN */}
            {/*<Route
              path="/opensource"
              render={(props) => (
                <Opensource {...props} theme={this.props.theme} />
              )}
            />*/}
            {/*<Route
              path="/splash"
              render={(props) => <Splash {...props} theme={this.props.theme} />}
            />*/}
          </Switch>
        </HashRouter>
      </div>
    );
  }
}
